/*
  ====================================================================
  Header
  ====================================================================
*/

.main-header {
    min-height: 885px;
    background: url("../img/content/bg.png") no-repeat;
    background-position: center center;
    background-size: cover;
    background-attachment: fixed;
    width: 100%;
    .row{
        height: 445px;
        position: relative;
    }
    &__video-main{
        float: left;
        //width: 100px;
    }
    &__video-respons{
        display: none;
    }
    &__title{
        padding-top: 44px;
        padding-left: 36px;
        float: left;
        width: 229px;
        text-align: center;
        margin: 0 auto;
    }

    &__link {
        width: 130px;
        position: relative;
        padding: 3px 0 11px 14px;
        margin: 0 auto;
        .btn {
            text-decoration: none;
            width: 100%;
            min-height: 20px;
            text-align: center;
            font-family: 'Arial', sans-serif;
            font-size: 14px;
            line-height: 40px;
            position: relative;
            color: $white;
            z-index: 2;
            cursor: pointer;
            display: inline-block;
            border-radius: 13px;
            background: $gradient1;
            background: -webkit-linear-gradient($gradient1, $gradient2);
            background: -o-linear-gradient($gradient1, $gradient2);
            background: -moz-linear-gradient($gradient1, $gradient2);
            background: linear-gradient($gradient1, $gradient2);
            transition: $transition;
            &:hover{
                background: $gradient2;
                background: -webkit-linear-gradient($gradient2, $gradient1);
                background: -o-linear-gradient($gradient2, $gradient1);
                background: -moz-linear-gradient($gradient2, $gradient1);
                background: linear-gradient($gradient2, $gradient1);
                transition: $transition;
            }

        }
    }

    &__btn{
        width: 169px;
        position: relative;
        top: 27px;
        left: 25px;
        .btn {
            text-decoration: none;
            font-style: italic;
            width: 100%;
            min-height: 40px;
            text-align: center;
            padding: 8px;
            font-family: 'Arial',sans-serif;
            font-size: 24px;
            line-height: 40px;
            margin-bottom: 20px;
            position: relative;
            color: $white;
            z-index: 2;
            cursor: pointer;
            display: inline-block;
            vertical-align: middle;
            transform: perspective(1px) translateZ(0);
            box-shadow: 0 0 1px transparent;
            transition-property: color;
            transition-duration: 0.3s;
            background-color: $orange;
            border: 4px solid $orange;
            border-radius: 10px;
            &:before{
                content: "";
                position: absolute;
                z-index: -1;
                top: 0;
                bottom: 0;
                left: 0;
                right: 0;
                border-radius: 10px;
                background: $white;
                transform: scaleY(0);
                transform-origin: 50%;
                transition-property: transform;
                transition-duration: 0.3s;
                transition-timing-function: ease-out;
            }
        }
        .btn:hover:before, .btn:focus:before, .btn:active:before {
            transform: scaleY(1);
        }
        .btn:hover, .btn:focus, .btn:active {
            color: $orange;
            border: 4px solid $orange;
        }

    }
    &__item{
        ul{
            height: 280px;
        }
        li{
            width: 273px;
            float: left;
            border-radius: 100%;
            transition: $transition;
            filter: grayscale(0);
            &:nth-child(2){
                margin: 0 28px;
            }
            &:hover{
                transform: scale(0.95);
                transition: $transition;
                filter: grayscale(100%);
            }
        }
        img{
            width: 250px;
            border-radius: 100%;
        }
    }
    &__text{
        text-align: center;
        margin: 0 auto;
        //width: 600px;
        border-bottom: 2px solid $orange;
        .button{
            border-radius: 100%;
            background: $gradient2;
            width: 40px;
            height: 40px;
            margin: 0 auto;
            position: relative;
            top: 20px;
            transition: $transition;
            img{
                position: relative;
                top: 10px;
                transition: $transition;
                filter: grayscale(0);
                &:hover{
                    transition: $transition;
                    filter: grayscale(100%);
                }
            }
            &:hover{
                transform: scale(0.9);
                transition: $transition;
                top: 30px;
            }
        }
    }
    span{
        color: $orange;
    }
    h5{
        padding: 10px;
        background: $gradient1;
        width: 340px;
        margin: 20px auto -5px auto;
        border-radius: 18px;
        a{
            color: $orange;
            text-decoration: underline;
            transition: $transition;
            &:hover{
                text-decoration: none;
                color: $white;
                transition: $transition;
            }
        }
    }
}

@media (max-width: 900px) {
    .main-header {
        min-height: 1285px;
        .row{
            height: 820px;
        }
        &__video-main{
            display: none;
        }
        &__video-respons{
            display: block;
        }
        &__title {
            float: none;
            width: 90%;
            padding-left: 0;
        }
        &__btn {
            left: 0;
            margin: 0 auto;
            padding-bottom: 40px;
        }
        iframe{
            width: 100%;
        }
        &__item{
            ul{
                padding-top: 40px;
            }
            li{
                width: 230px;
                &:hover{
                    transform: scale(1);
                    transition: $transition;
                    filter: grayscale(0);
                }
            }
        }
    }
}

@media (max-width: 767px) {
    .main-header {
        &__item{
            ul{
                height: 820px;
            }
            li{
                padding-bottom: 20px;
                margin: 0 auto;
                float: none;
                &:nth-child(2){
                    margin: 0 auto;
                    text-align: center;
                }
            }
        }
        &__text{
            margin-bottom: 30px;
            h5{
                width: 212px;
                border-radius: 33px;
            }
        }
    }
}

