    ul{
        padding-top: 34px;
        height: 50px;
    }
    &__left{
        float: left;
    }
    &__right{
        float: right;
        opacity: 0.8;
    }
    li{
        float: left;
        padding-left: 15px;
        font-size: 12px;
     }
    img{
        position: relative;
        top: -5px;
        transition: $transition;
