// GUI stylies of the project
// For example, styles of the links, buttons and so on.


.container{
    width: 900px;
    margin: 0 auto;
    height: 100%;
    position: relative;
    box-sizing: border-box;
    @include respond-to(tablet){
        width: 100%;
        //padding: 0 20px;
    }
    @include respond-to(mobile){
        width: 100%;
    }

}


//loader
#loader{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    //opacity: 0;
}
.dot{
    width: 12px;
    height: 12px;
    display: inline-block;
    border-radius: 100%;
    margin-right: 6px;
    border: 2px solid $orange;
}
 .main-header{
     .row{
         opacity: 0;
     }
     li{
         opacity: 0;
         transform: scale(0.55);
         rotation: 70deg;
     }
     &__text{
         opacity: 0;
         position: relative;
     }
}

.hideme{
    opacity: 0;
}

@keyframes grounUP_line {
    10% {width: 0;}
    100% {width: 230px;}
}

